import AddEditTeams from "./AddEditTeams";
export default {
  name: "Admin",
  props: {
    teams: { type: Array },
    events: { type: Object },
    players: { type: Array }
  },
  data() {
    return {
      addEditTeams: false,
      mode: "",
      team: {
        name: "",
        img: "",
        playerIds: [],
        eventStats: {}
      }
    };
  },
  mounted() {
  },
  methods: {
    editTeam(team) {
      this.team = team;
      this.mode = "edit";
      this.addEditTeams = true;
    },
    addTeam(team) {
      this.team = team;
      this.mode = "add";
      this.addEditTeams = true;
    },
    deleteTeam(team) {
      this.deletingDialogOpen = true
      this.$buefy.dialog.confirm({
        message: `Are you sure you want to delete team ${team.name}`,
        type: "is-dark",
        onConfirm: () => {
          this.$axios(
          process.env.baseUrl + `api/teams/${team._id}`,
          {
            method: "delete"
          }
        )
        .then(function(response) {
          return response.data;
        })
        .then(response => {
          if (response.code !== undefined) {
            this.$buefy.snackbar.open(response.message);
          } else {
            window.location.reload();
          }
        });
        },
        onCancel: () => {
          this.deletingDialogOpen = false
        }
      });
    }
  },
  components: { AddEditTeams }
};
