import AddEditEvents from "./AddEditEvents";
export default {
  name: "AdminEvents",
  props: {
    events: { type: Array },
    teamsObj: { type: Object },
    teams: { type: Array }
  },
  data() {
    return {
      addEditEvents: false,
      showGroups: false,
      mode: "",
      event: {
        files: [],
        name: "",
        details: "",
        date: {
          from: "",
          to: ""
        },
        teamIds: [],
        teamsObj: {},
        img: "",
        matchIds: [],
        groups: [],
        active: true,
        activePlayerIds: [],
        NofTeamsAdvanceInGroup: 0,
        flag: "",
        location: "",
        numberOfTeams: 0
      }
    };
  },
  created() {
  },
  methods: {
    editEvent(event) {
      this.event = event;
      this.mode = "edit";
      this.addEditEvents = true;
    },
    addEvent() {
      this.mode = "add";
      this.addEditEvents = true;
    }
  },
  components: { AddEditEvents }
};
